@font-face {
    font-family: 'notosans';
    src: url(../font/NotoSansKR-Regular.otf);
}

html, body {
    height: 100%;
}

ul, li, p, div, span, a, li a, ul li a {
    padding: 0;
    margin: 0;
    list-style: none;
    box-sizing: border-box;
    text-align: center;
    font-family: 'notosans';
}

a, ul li a, .header .menu ul li a,.header .logo a,.header  ul li a {
    text-decoration: none;
    color: #333;
}

a:hover {
    text-decoration: none;
    color: #333;
}

select, input, button {
    outline: none;
}